/* make variables */
* {
  margin: 0;
  padding: 0;
}
:root {
  --lightgray: #D9D9D9;
  --gray: #9c9c9c;
  --orange: #8458B3;
  --darkGrey: #464D53;
  --caloryCard: #656565;
  --planCard: linear-gradient(210.41deg, #270042 1.14%, #FF00FF 100.75%);
  --appColor:  #494D5F;

  --bg-color:#270042;
  --text-color:#d0bdf4;
  --heading-color:#EE82EE;
  --card-color:#725472;
  --card-hover:#FF00FF;
  --logo-color:#E6E6FA;
  --patch-color:#D8BFD8;
  --btn-color:#9400D3;
  --btn-hover:#EE82EE;
  --transition-card:200ms ;
  --diffrent:rgb(255, 115, 0);
}

.App {
  background-image: url(data:image/png;base64,);
  mix-blend-mode: overlay;
  background-color: var(--bg-color);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
::-webkit-scrollbar{
  display: none;
}

.stroke-text{
  color: transparent;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: white;
}

.btn{
  background-color: white;
  padding: 0.5rem;
  font-weight: bold;
  border: 4px solid transparent;
  transition: 300ms all;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn:hover{
  cursor: pointer;
}

.blur{
  background: rgba(253, 120, 43, 0.69);
  position: absolute;
  border-radius: 50%;
  filter: blur(160px);
  z-index: -9;
}