.Footer-container{
    position: relative;
}
.Footer-container>hr{
    border: 1px solid var(--lightgray);
}
.footer{
    pad: 1rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    align-items: center;
    justify-content: center;
    height: 20rem;
}
.social-links{
    display: flex;
    gap: 4rem;
}
.social-links>img{
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}

.logo-footer>img{
    width: 10rem;
}


.footer-blur-1{
    bottom: 0;
    right: 25%;
    width: 26rem;
    height: 12rem;
    filter: blur(200px);
    background: red;
  }

  .footer-blur-2{
    bottom: 0;
    left: 25%;
    width: 26rem;
    height: 12rem;
    filter: blur(200px);
    background: orange;
  }